import * as _ from "lodash";
import "../styles/all.scss";
import { Page } from "./pages/AbstractPage";
import { IndexPage } from "./pages/IndexPage";
import { AboutPage } from "./pages/AboutPage";
import { FamilyPage } from "./pages/FamilyPage";
import { Header } from "./components/Header";
import { getMobileOperatingSystem } from "./utils/DetectBrowser";
document.addEventListener("DOMContentLoaded", () => {
    appendBaseHref();
    initPage();
    initHeader();

    window.addEventListener("resize", handleResize);
    window.addEventListener("scroll", handleScroll);
    // handleResize();
});

const handleResize = () => {
    if (header) header.handleResize();
    if (currentPage) currentPage.handleResize();
    createSocialLink();
    handleScroll();
};

const handleScroll = () => {
    if (header) header.handleScroll(window.pageYOffset);
};

/*
 * Set HTML baseHref
 */
const appendBaseHref = () => {
    let base = document.createElement("base");
    base.href =
        window.location.protocol +
        "//" +
        window.location.hostname +
        (window.location.port ? ":" + window.location.port : "");
    console.log(base.href);
    document.getElementsByTagName("head")[0].appendChild(base);
};

/*
 * Header
 */
let header: Header;
const initHeader = () => {
    header = new Header();
    header.init();
};

/*
 * Pages
 */
let currentPage: Page;
const initPage = () => {
    const pageName = window.location.pathname;
    // console.log(pageName, pageName.includes("index"));
    if (
        pageName === "/" ||
        pageName === "/en/" ||
        pageName === "/en" ||
        pageName.includes("index")
    ) {
        currentPage = new IndexPage(`indexPage`);
        currentPage.init();
    }
    if (pageName.includes("about")) {
        currentPage = new AboutPage(`aboutPage`);
        currentPage.init();
    }
    if (pageName.includes("family")) {
        currentPage = new FamilyPage(`familyPage`);
        currentPage.init();
    }

    //
    createSocialLink();
};

function createSocialLink() {
    let fbUrl = "https://www.facebook.com/ONElabTW/?modal=admin_todo_tour";
    if (getMobileOperatingSystem() === "iOS") {
        fbUrl = "fb://page/?id=120283498684763";
    }
    if (getMobileOperatingSystem() === "Android") {
        fbUrl = "fb://page/120283498684763";
    }
    const fbLinks = document.querySelectorAll(".social-link-facebook");
    Array.prototype.slice.call(fbLinks).map((link: any) => {
        link.setAttribute("href", fbUrl);
    });
}
