import { Page } from "./AbstractPage";
import * as $ from "jquery";
import "slick-carousel";
import "slick-carousel/slick/slick.css";

export class FamilyPage extends Page {
    constructor(name: string) {
        super(name);
    }
    init() {
        super.init();
        this.createCarousel();
    }

    createCarousel() {
        $(".event-carousel").slick({
            dots: false,
            infinite: true,
            speed: 300,
            autoplay: true,
            autoplaySpeed: 2000,
            slidesToShow: 4,
            slidesToScroll: 1,
            arrows: false,
            responsive: [
                {
                    breakpoint: 749,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 1,
                    },
                },
            ],
        });
    }
}
