export class Header {
    private element: HTMLElement;
    private toogle: HTMLButtonElement;
    private collapse: HTMLElement;
    private logo: HTMLAnchorElement;
    private nav: HTMLElement;
    private overlay: HTMLElement;
    private isCollapseActive: Boolean;

    constructor() {
        this.isCollapseActive = false;
        this.element = document.getElementById("header");
        this.toogle = this.element.querySelector(".header-collapse-toggle");
        this.collapse = this.element.querySelector(".header-collapse");
        this.logo = this.element.querySelector(".header-logo");
        this.nav = this.element.querySelector(".header-nav");
        this.overlay = this.element.querySelector(".header-collapse-overlay");
    }

    handleResize() {
        this.isCollapseActive = false;
        this.activeNav();
    }
    handleScroll(windowScrollTop: number) {
        // console.log("windowScrollTop", windowScrollTop);
        if (windowScrollTop > window.innerHeight / 4) {
            this.element.classList.add("sticky");
        } else {
            this.element.classList.remove("sticky");
        }
    }

    init() {
        this.toogle.addEventListener("click", (e: MouseEvent) => {
            this.isCollapseActive = !this.isCollapseActive;
            this.activeNav();
        });
        this.overlay.addEventListener("click", (e: MouseEvent) => {
            this.isCollapseActive = false;
            this.activeNav();
        });
    }
    private activeNav() {
        if (this.isCollapseActive) {
            this.toogle.classList.add("active");
            this.collapse.classList.add("active");
        } else {
            this.toogle.classList.remove("active");
            this.collapse.classList.remove("active");
        }
    }
}
